.download-page-edit-block {
    background-color: #DBEDF3;
    padding: 20px;
    border-radius: 10px;
    border: 1px solid #404B69;

    .download-page-headline {
        color: #F73859;
        font-size: 22px;
        margin-top: 0;
        font-weight: bold;
    }

    .download-page-text {
        margin-bottom: 10px;
        font-size: 20px;
        color: #404B69;
    }

    .element-delete {
        text-decoration: none;
        color: #F73859;
        font-size: 24px;
    }

    .element-add {
        background-color: #404B69;
    }
}

